<template >
    <div class="elem"
        :class="[currentLvlClassName, stateClassName, isActiveClassName]"
        :data-index="index"
        @click="toggleActive" >
        {{tagName + classListString}}
    </div>
</template>

<script>
export default {
    name: 'ConstructorElem',
    props: {
        tagName: {type: String, default: ''},
        lvl: {type: Number, default: 2},
        index: Number,
        isDisabled: {type: Boolean, default: false},
        activeIndex: {type: [Boolean, Number]},
        elemClassList: {type: [Array, DOMTokenList]}
    },
    data() {
        return {
            // left: 0,
            // top: 0,
            isActive: false
        }
    },
    computed: {
        stateClassName() {
            if (this.isDisabled) return 'elem--disabled';
            else return '';
        },
        currentLvlClassName() {
            if (this.isDragging) return 'lvl-1';
            else return 'lvl-' + this.lvl;
        },
        isActiveClassName() {
            if (this.isActive && this.index === this.activeIndex) return 'active';
            else return '';
        },
        classListString() {
            if (this.elemClassList.length > 0) {
                // console.log(this.elemClassList);
                let arr = [];
                this.elemClassList.forEach((str) => {
                    arr.push(str);
                });
                return '.' + arr.join('.');
            }
            else return '';
        }
    },
    watch: {
        // isActive: function() {
        //     if (this.isActive) {
        //         this.$emit('set-active-elem', this.index);
        //     } else {
        //         this.$emit('set-active-elem', false);
        //     }
        // },
        activeIndex: function() {
            if (this.activeIndex === this.index) this.isActive = true; 
            else this.isActive = false;
        }
    },
    methods: {
        toggleActive() {
            if (!this.isDisabled) {
                if (this.activeIndex !== false && this.activeIndex !== this.index) {
                    this.$emit('set-active-elem', this.index);
                } else if (this.activeIndex !== false && this.activeIndex === this.index) {
                    this.$emit('set-active-elem', false);
                } else if (this.activeIndex === false) {
                    this.$emit('set-active-elem', this.index);
                }
            }
        }
    }
}
</script>

<style lang="scss">
.elem {
    position: relative;
    z-index: 2;
    background-color: rgba(189, 226, 231, 0.6);
    padding: 2px 7px;
    width: fit-content;
    border-radius: 3px;
    user-select: none;
    transition-property: background-color, box-shadow;
    transition-duration: .3s, .3s;
    font-size: 12px;
    font-family: monospace;
    font-weight: 700;
    cursor: pointer;
    // &:not(.elem--disabled) {
    //     cursor: grab;
    // }
    &:not(.elem--disabled):hover {
        background-color: rgba(189, 226, 231, 0.7);
        box-shadow: 0 0 5px 0 rgba(189, 226, 231, 0.6);
    }
    &.active, &.active:hover {
        background-color: rgba(138, 180, 243, 0.9);
        box-shadow: 0 0 10px 2px rgba(94, 184, 245, 0.8);
    }
    // &.dragging {
    //     position: absolute;
    //     z-index: 5;
    //     cursor: grabbing;
    // }
    &--disabled {
        background-color: rgba(155, 155, 155, 0.6);
    }
    &:not(.elem--disabled):not(.dragging)::before {
        content: '';
        display: block;
        height: 2px;
        position: absolute;
        z-index: 1;
        right: 100%;
        top: 10px;
        background-color: #ccc;
    }
    // &--shadow {
    //     border: 2px dashed rgb(189, 226, 231);
    //     background-color: transparent;
    // }

}
</style>