<template>
    <div class="datalist popup__datalist active">
        <div v-for="(item, ind) in list"
            :key="ind"
            :data-content="item"
            @mousedown="setInputValue(ind)"
            class="datalist__item" > 
            {{item}}
        </div>
    </div>
</template>

<script>
export default {
    props: ['list', 'inputValue'],
    name: 'PopupDatalist',
    methods: {
        setInputValue(i) {
            let val = this.list[i];
            this.$emit('set-input-value', val);
        }
    }

}
</script>

<style lang="scss" scoped>
.datalist {
    background-color: #fff;
    box-shadow: 0 5px 5px 0 rgba(0, 0, 0, 0.286);
    display: none;
    &__item {
        padding: 4px 10px;
        cursor: pointer;
        font-family: monospace;
        font-weight: 700;
        &:hover {
            background-color: rgb(236, 236, 236);
        }
    }
    &.active {
        display: block;
    }
}
</style>