<template>
    <div class="popup card" :class="{'active': isActive}" id="popupAddElem">
        <h2 class="popup__title">{{popupTitle}}</h2>
        <div class="popup__row">
            <popup-input 
                :withDatalist="true"
                @load-datalist="loadDatalistTags"
                @get-value="tagValue = $event"
                :list="tags"
                :isInvalid="tagIsInvalid"
                :inputParams="{placeholder: 'Tag', name: 'elem-tag'}"
                ref="elemTag" />
            <btn-submit-input
                @submit-input-value="submitTag"
                :btnName="'submit-tag'"
                @refresh-preview="refreshPreview" />
        </div>
        <div class="popup__row">
            <popup-input
                :withDatalist="false"
                :inputParams="{placeholder: 'Class', name: 'elem-class'}"
                :isDisabled="fullFormIsDisabled"
                @get-value="classValue = $event"
                ref="elemClass" />
            <btn-submit-input
                @submit-input-value="submitClass"
                :btnName="'submit-class'"
                @refresh-preview="refreshPreview" />
        </div>
        <div class="popup__row">
            <popup-input
                :withDatalist="true"
                :inputParams="{placeholder: 'Attribute', name: 'elem-attr'}"
                :isDisabled="fullFormIsDisabled"
                @get-value="attrValue = $event"
                @load-datalist="loadDatalistAttrs"
                :list="attrs"
                :isInvalid="attrIsInvalid"
                ref="elemAttr" />
            <popup-input
                :withDatalist="false"
                :inputParams="{placeholder: 'Value', name: 'elem-attr-val'}"
                :isDisabled="fullFormIsDisabled"
                @get-value="attrValValue = $event"
                ref="elemAttrVal" />

            <btn-submit-input
                @submit-input-value="submitAttr"
                :btnName="'submit-attr'"
                @refresh-preview="refreshPreview" />

        </div>
        <popup-preview
            :previewContent="previewContent" />
            
        <popup-footer
            @clear-all="clearAll()"
            @test-elems="testElems"
            :addBtnDisabled="addBtnDisabled"
            @add-element="addElementToList" />
        <div class="popup__close" @click="togglePopup()" ></div>
    </div>
</template>

<script>
import PopupInput from '@/components/PopupInput.vue';
import BtnSubmitInput from '@/components/BtnSubmitInput.vue';
import PopupPreview from '@/components/PopupPreview.vue';
import PopupFooter from '@/components/PopupFooter.vue';

export default {
    name: 'PopupWindow',
    props: ['isActive'],
    components: {
        PopupInput,
        BtnSubmitInput,
        PopupPreview,
        PopupFooter
    },
    data() {
        return {
            popupTitle: 'Add element',
            tags: [], attrs: [],
            tagsFullList: undefined, attrsFullList: undefined,
            tagValue: '', attrValue: '', attrValValue: '', classValue: '',
            tagIsInvalid: false, attrIsInvalid: false,
            newElement: null,
            previewContent: '',
            addBtnDisabled: true
        }
    },
    computed: {
        fullFormIsDisabled: function() {
            return !(this.newElement instanceof HTMLElement);
        }
    },
    watch: {
        tagValue: async function() {
            await this.loadDatalistTags();
            this.tags = this.tagsFullList.filter(elem => elem.startsWith(this.tagValue));
            this.tags = this.tags.slice(0, 5);
        },
        attrValue: async function() {
            await this.loadDatalistAttrs();
            this.attrs = [].concat(this.attrsFullList['global']);
            if (this.attrsFullList[this.tagValue]) {
                this.attrs = this.attrs.concat(this.attrsFullList[this.tagValue]);
            }
            // console.log(this.attrs);
            this.attrs = this.attrs.filter(elem => elem.startsWith(this.attrValue));
            this.attrs = this.attrs.slice(0, 5);
        },
        newElement: function() {
            if (this.newElement instanceof HTMLElement) this.addBtnDisabled = false;
            else this.addBtnDisabled = true;
        }
    },
    methods: {
        togglePopup() {this.$emit('toggle-popup')},
        refreshPreview() {
            if (this.newElement instanceof HTMLElement) {
                this.previewContent = this.newElement.outerHTML;
                window.refreshHighlight();
            }
        },
        async submitTag() {
            if (!this.tagsFullList) {
                await this.loadDatalistTags();
            }
            if (this.tagsFullList.includes(this.tagValue)) {
                let elem = document.createElement(this.tagValue);
                this.newElement = elem;
                // this.fullFormIsDisabled = false;
                // console.log(this.newElement);
            } else {
                this.tagIsInvalid = false;
                this.tagIsInvalid = true;
                setTimeout(() => {
                    this.tagIsInvalid = false;
                }, 2000);
            }
        },
        submitClass() {
            this.newElement.classList.add(this.classValue);
            this.$refs.elemClass.reset();
        },
        clearAll() {
            this.$refs.elemTag.reset();
            this.$refs.elemClass.reset();
            this.$refs.elemAttr.reset();
            this.$refs.elemAttrVal.reset();
            this.newElement = null;
            this.previewContent = '';
        },
        addElementToList() {
            this.$emit('add-element-to-list', this.newElement);
            // console.log(this.newElement);
            this.clearAll();
            // console.log(this.newElement);
        },
        async submitAttr() {
            if (this.attrsFullList === undefined) {
                await this.loadDatalistAttrs();
            }
            let checkList = []
                .concat(this.attrsFullList['global'], 
                (this.attrsFullList[this.tagValue] || []));
                
            // console.log(checkList);

            if (checkList.includes(this.attrValue)) {
                this.newElement.setAttribute(this.attrValue, this.attrValValue);
                this.$refs.elemAttr.reset();
                this.$refs.elemAttrVal.reset();
            } else {
                this.attrIsInvalid = false;
                this.attrIsInvalid = true;
                setTimeout(() => {
                    this.attrIsInvalid = false;
                }, 2000);
            }
        },
        async loadDatalistTags() {
            if (this.tagsFullList === undefined) {
                await this.getHtmlTags();
            }
            this.tags = this.tagsFullList.slice(0, 5);
        },
        async loadDatalistAttrs() {
            let tag = this.tagValue;
            if (this.attrsFullList === undefined) {
                await this.getHtmlAttrs();
            }
            this.attrs = [].concat(this.attrsFullList['global']);
            if (tag && this.attrsFullList[tag]) {
                // console.log(true);
                this.attrs = this.attrs.concat(this.attrsFullList[tag]);
            }
            // console.log(this.attrs);
            this.attrs = this.attrs.slice(0, 5);
            this.attrs.sort();
            // console.log(this.attrsFullList);
        },
        async getHtmlTags() {
            let response = await fetch('./JSON-html-tags/tags.json');
            let arr = await response.json();
            arr = arr.map(elem => elem.slice(1, -1));
            this.tagsFullList = arr.sort();
        },
        async getHtmlAttrs() {
            let response = await fetch('./JSON-html-tags/attrs/_full-list.json');
            let attrs = await response.json();
            for (let tag in attrs) {
                attrs[tag] = attrs[tag].sort();
            }
            this.attrsFullList = attrs;
        },
        /* JUST FOR TESTING, REMOVE BEFORE PRODUCTION BUILDING */
        /* JUST FOR TESTING, REMOVE BEFORE PRODUCTION BUILDING */
        /* JUST FOR TESTING, REMOVE BEFORE PRODUCTION BUILDING */
        testElems() {
            let elem1 = document.createElement('abbr');
            let elem2 = document.createElement('address');
            let elem3 = document.createElement('input');
            let elem4 = document.createElement('button');
            let elem5 = document.createElement('select');
            this.$emit('add-element-to-list', elem1);
            this.$emit('add-element-to-list', elem2);
            this.$emit('add-element-to-list', elem3);
            this.$emit('add-element-to-list', elem4);
            this.$emit('add-element-to-list', elem5);
            this.clearAll();
        }
    },
}
</script>

<style lang="scss">
.popup {
    position: absolute;
    z-index: 10;
    top: 30px;
    left: 30px;
    bottom: 30px;
    right: 30px;
    display: flex;
    // display: none;
    background-color: #fff9fc;
    flex-direction: column;
    align-items: flex-start;
    &.active {
        display: flex;
        animation: showingPopup .2s forwards;
    }  
    &__title {
        text-align: center;
        margin-bottom: 15px;
        align-self: stretch;
    }
    &__subtitle {
        text-align: center;
        margin-bottom: 15px;
    }
    &__row {
        display: flex;
        justify-content: center;
        margin-bottom: 50px;
    }
    &__input-wrap {
        position: relative;
        & + & {
        margin-left: 30px;
        }
    }
    &__input {
        margin-bottom: 0 !important;
    }
    &__datalist {
        position: absolute;
        z-index: 10;
        top: 100%;
        left: 0;
        width: 100%;
    }
    &__preview {
        text-align: center;
        margin-top: 20px;
        font-size: 15px;
        // outline: 1px solid red;
        flex-grow: 1;
        align-self: stretch;
    }
    &__btn.btn--float {
        width: 30px;
        height: 30px;
        background-color: #45f494;
        background-image: none;
        transition: box-shadow .2s;
        &:focus {
            box-shadow: 0px 0px 8px 2px #006aff;
        }
    }
    &__footer {
        display: flex;
        justify-content: space-between;
        align-self: stretch;
    }
    &__close {
        width: 40px;
        height: 40px;
        position: absolute;
        right: 15px;
        top: 15px;
        cursor: pointer;
        transition: .15s;
        &:hover {
            transform: scale(1.2);
        }
        &::before, &::after {
            content: '';
            display: block;
            position: absolute;
            background-color: #000000;
            height: 4px;
            width: 40px;
            top: calc(50% - 2px);
            left: 0;
            transform-origin: center center;
            border-radius: 4px;
        }
        &::before {
            transform: rotate(45deg);
        }
        &::after {
            transform: rotate(-45deg);
        }
    }
}



@keyframes showingPopup {
    from {
        transform: scale(0.7);
        opacity: 0;
    }
    to {
        transform: scale(1);
        opacity: 1;
    }
}
</style>