<template>
    <div class="popup__input-wrap">
        <input 
            class="popup__input"
            :class="{invalid: isInvalid}"
            :placeholder="inputParams.placeholder || ''" 
            :name="inputParams.name || ''" 
            :disabled="isDisabled || false"
            type="text" 
            autocomplete="off"
            @focus="loadDatalist(), toggleDatalist()"
            @blur="toggleDatalist()"
            v-model="inputValue" />
        <popup-datalist 
            v-if="withDatalist && datalistActive"
            :list="list"
            :inputValue="inputValue"
            @set-input-value="setInputValue" />
    </div>
</template>

<script>
import PopupDatalist from "@/components/PopupDatalist.vue";

export default {
    name: 'PopupInput',
    props: {
        withDatalist: Boolean,
        list: Array,
        isInvalid: Boolean,
        inputParams: Object,
        isDisabled: Boolean,
    },
    components: {
        PopupDatalist
    },
    data() {
        return {
            datalistActive: false,
            inputValue: ''
        }
    },
    methods: {
        loadDatalist() {
            if (this.withDatalist) this.$emit('load-datalist');
        },
        toggleDatalist() {
            this.datalistActive = !this.datalistActive;
        },
        setInputValue(val) {
            this.inputValue = val;
        },
        reset() {
            this.inputValue = '';
        }
    },
    watch: {
        inputValue: function() {
            this.$emit('get-value', this.inputValue);
        }
    },
    

}
</script>

