<template>
    <div class="popup__input-wrap">
        <button 
            class="btn--float popup__btn" 
            :name="btnName"
            @click="submitInputValue" >{{content}}</button>
    </div>
</template>

<script>
export default {
    name: 'BtnSubmitInput',
    props: ['btnName'],
    data() {
        return {
            content: '+',
        }
    },
    methods: {
        submitInputValue() {
            this.$emit('submit-input-value');
            this.$emit('refresh-preview');
        }
    }
}
</script>

