<template>
    <div class="fields__item card preview" >
        <div v-for="(line, ind) of linesStart"
            :key="line"
            class="preview__line"
            :class="getLvlClassNameStartLines(ind)" >
            {{line}}
        </div>

        <div 
            class="preview__line"
            v-for="(line, ind) of lines"
            :class="[getLvlClassNameElems(), getActiveLineClassName(ind)]"
            :key="line.id" >
            {{ line.html }}
        </div>

        <div v-for="(line, ind) of linesEnd"
            :key="line"
            class="preview__line"
            :class="getLvlClassNameEndLines(ind)" >
            {{ line }}
        </div>
    </div>
</template>

<script>
export default {
    name: 'PreviewCode',
    props: ['DOMelems', 'activeIndex'],
    data() {
        return {
            linesStart: [
                '<!DOCTYPE html>',
                '<html lang="en">',
                '<head>',
                '<meta charset="UTF-8">',
                '<meta name="viewport" content="width=device-width, initial-scale=1.0">',
                '<title>Document</title>',
                '</head>',
                '<body>'
            ],
            linesEnd: [
                '</body>',
                '</html>'
            ],
            lines: [],
        }
    },
    watch: {
        DOMelems: function() {
            this.lines = this.DOMelems.map((obj, i) => {
                let newObj = {
                    html: obj.el.outerHTML,
                    id: Date.now() + Math.random() * 100 + i * 100
                };
                return newObj;
            });
        },
    },
    updated() {
        this.$nextTick(function () {
            window.refreshHighlight();
        });
    },
    mounted() {
        this.$nextTick(function () {
            window.refreshHighlight();
        });
    },
    methods: {
        getLvlClassNameElems() {
            return 'lvl-3';
        },
        getLvlClassNameStartLines(i) {
            let lvl2Inds = [2, 6, 7];   // -2
            let lvl3Inds = [3, 4, 5];
            if (lvl2Inds.includes(i)) return 'lvl-2';
            else if (lvl3Inds.includes(i)) return 'lvl-3';
        },
        getLvlClassNameEndLines(i) {
            if (i === 0) return 'lvl-2';
        },
        getActiveLineClassName(i) {
            if (i === this.activeIndex) {
                return 'active';
            }
        }
    }
}
</script>

<style lang="scss">
.preview {
    &__line {
        position: relative;
        font-size: 12px;
        // white-space: pre;
    }
    &__line.active {
        background-color: rgba(131, 245, 253, 0.3);
    }
}
</style>