<template >
    <div class="fields__item card constructor" id="constructor"  >
        <constructor-elem 
            :tagName="'body'"
            :isDisabled="true"
            :elemClassList="[]"
            :lvl="1" >
        </constructor-elem>
    
        <constructor-elem
            v-for="(elem, i) in elems" :key="i"
            :tagName="elem.tag"
            :lvl="elem.lvl"
            :index="i"
            :activeIndex="activeIndex"
            :elemClassList="elem.classList"
            @set-active-elem="setActiveElem" >
        </constructor-elem>
    </div>
</template>

<script>
import ConstructorElem from '@/components/ConstructorElem.vue';

export default {
    name: 'AppConstructor',
    props: ['DOMelems', 'activeIndex'],
    components: {
        ConstructorElem
    },
    watch: {
        DOMelems: function() {
            // this.elems = [];
            this.elems = this.DOMelems.map((obj) => {
                let elem = {
                    tag: obj.el.tagName.toLowerCase(),
                    lvl: obj.lvl,
                    classList: obj.el.classList
                };
                return elem;
                // this.elems.push(elem);
            });
            // console.log(this.elems);
        }
    },
    data() {
        return {
            elems: [],
        }
    },
    methods: {
        setActiveElem(ind) {
            this.$emit('set-active-elem', ind);
        },
        refreshElems() {
            // this.elems = [];
            this.elems = this.DOMelems.map((obj) => {
                let elem = {
                    tag: obj.el.tagName.toLowerCase(),
                    lvl: obj.lvl,
                    classList: obj.el.classList
                };
                return elem;
                // this.elems.push(elem);
            });
        }
    }
}
</script>

<style lang="scss">
.constructor {
    position: relative;
    &::before {
        content: '';
        display: block;
        height: calc(100% - 64px);
        width: 2px;
        position: absolute;
        z-index: 1;
        left: 24px;
        top: 44px;
        background-color: #ccc;
    }
    // &__item {}
}


</style>