<template>
    <div class="tools">
        <button 
            class="btn--raised" 
            name="add-element" 
            @click="$emit('toggle-popup')" >
            Add element
        </button>
        <button 
            class="btn--raised"
            name="clear-constructor"
            @click="$emit('clear-constructor')" >
            Clear constructor
        </button>
        <div class="elem-tools">
            <template v-if="activeElem !== false">
                <button 
                    class="elem-tools__btn elem-tools__btn--ar-up"
                    @click="editDomElems('orderUp')" ></button>
                <button 
                    class="elem-tools__btn elem-tools__btn--ar-down"
                    @click="editDomElems('orderDown')" ></button>
                <button 
                    class="elem-tools__btn elem-tools__btn--ar-left"
                    @click="editDomElems('lvlDown')" ></button>
                <button 
                    class="elem-tools__btn elem-tools__btn--ar-right"
                    @click="editDomElems('lvlUp')" ></button>
                <button 
                    class="elem-tools__btn elem-tools__btn--copy"
                    @click="editDomElems('clone')" ></button>
                <button 
                    class="elem-tools__btn elem-tools__btn--delete"
                    @click="editDomElems('delete')" ></button>
            </template>
            <template v-else>Element wasn't selected</template>
        </div>
        <div class="validator">
            <img 
                v-if="isValid" 
                class="validator__img" 
                src="@/assets/check.png" 
                alt="constructor is valid"
                title="Constructor is valid">
            <img 
                v-else 
                class="validator__img" 
                src="@/assets/error.png" 
                alt="constructor is invalid"
                title="Constructor is invalid">
        </div>
    </div>
</template>

<script>
export default {
    name: 'ToolsPanel',
    props: ['activeElem', 'isValid'],
    data() {
        return {
            // isValid: true
        }
    },
    methods: {
        // togglePopup() {
        //     this.$emit('toggle-popup');
        // },
        editDomElems(action) {
            switch (action) {
                case 'orderUp':
                    this.$emit('change-order-elem', 'up');
                    break;
                case 'orderDown':
                    this.$emit('change-order-elem', 'down');
                    break;
                case 'lvlUp':
                    this.$emit('change-lvl-elem', 'up');
                    break;
                case 'lvlDown':
                    this.$emit('change-lvl-elem', 'down');
                    break;
                case 'clone':
                    this.$emit('clone-elem');
                    break;
                case 'delete':
                    this.$emit('delete-elem');
                    break;
                default:
                    break;
            }
        }
    }
}
</script>

<style lang="scss">
.tools {
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.elem-tools {
    display: flex;
    padding: 5px;
    &__btn {
        display: block;
        width: 30px;
        height: 30px;
        outline: none;
        border-radius: 5px;
        background-color: rgba(0, 255, 233, 0.15);
        transition: .15s;
        &:hover {
            background-color: rgba(0, 255, 233, 0.65)
        }
        &:active {
            transform: scale(1.1);
        }
        & + & {
            margin-left: 5px;
        }
        &::after {
            content: '';
            background: url(../assets/arrow-up.png) no-repeat center / contain;
            display: block;
            width: 100%;
            height: 100%;
        }
        &--ar-up::after {transform: none;}
        &--ar-down::after {transform: rotate(180deg);}
        &--ar-left::after {transform: rotate(-90deg);}
        &--ar-right::after {transform: rotate(90deg);}
        &--copy::after {background-image: url(../assets/copy.png);}
        &--delete::after {background-image: url(../assets/delete.png);}
    }
}
.validator {
    &__img {
        width: 30px;
    }
}
</style>