<template>
    <div id="app" >
        <app-header />
        <main>
            <div class="container" >
                <div class="content">
                    <tools-panel 
                        :activeElem="activeElem"
                        :isValid="layoutIsValid"
                        @toggle-popup="togglePopup()" 
                        @change-order-elem="changeOrderElems"
                        @change-lvl-elem="changeLvlElem"
                        @clone-elem="cloneElem"
                        @delete-elem="deleteElem"
                        @clear-constructor="clearConstructor()" >
                    </tools-panel>

                        <div class="fields">
                            <div class="fields__col">
                                <div class="fields__title">{{titles.constructor}}</div>
                                <app-constructor 
                                    :DOMelems="elementsList"
                                    :activeIndex="activeElem"
                                    @set-active-elem="setActiveElem"
                                    ref="constructor" >
                                </app-constructor>
                            </div>
                            <div class="fields__col">
                                <div class="fields__title">{{titles.preview}}</div>
                                <preview-code
                                    :DOMelems="elementsList"
                                    :activeIndex="activeElem" >
                                </preview-code>
                            </div>
                        </div>
                </div>
            </div>
            <popup-window 
                v-if="this.popupIsActive"
                @toggle-popup="togglePopup()"
                :isActive="popupIsActive"
                @add-element-to-list="addElementToList" >
            </popup-window>
        </main>
        <app-footer />

    </div>
</template>

<script>
import AppHeader from "@/components/AppHeader.vue";
import AppFooter from "@/components/AppFooter.vue";
import ToolsPanel from "@/components/ToolsPanel.vue";
// import AppFields from "@/components/AppFields.vue";
import PopupWindow from "@/components/PopupWindow.vue";
import AppConstructor from '@/components/AppConstructor.vue';
import PreviewCode from '@/components/PreviewCode.vue';



export default {
    name: 'App',
    components: {
        AppHeader,
        AppFooter,
        ToolsPanel,
        // AppFields,
        PopupWindow,
        AppConstructor,
        PreviewCode,
    },
    data() {
        return {
            popupIsActive: false,
            elementsList: [],
            activeElem: false,
            layoutIsValid: true,
            finalLayout: null,
            titles: {
                constructor: 'Constructor',
                preview: 'Preview'
            }
        }
    },
    computed: {},
    watch: {
        elementsList: function() {
            this.validateLayout();
        }
    },
    methods: {
        togglePopup() {this.popupIsActive = !this.popupIsActive;},
        addElementToList(val) {
            let obj = {
                el: val,
                lvl: 2
            };
            // val.dataset.lvl = 2;
            this.elementsList.push(obj);
            this.togglePopup();
        },
        setActiveElem(val) {
            switch (val) {
                case 'prev':
                    this.activeElem -= 1;
                    break;
                case 'next':
                    this.activeElem += 1;
                    break;
                default:
                    this.activeElem = val;
                    break;
            }
            this.validateLayout();
        },
        changeOrderElems(dir) {
            let len = this.elementsList.length;
            let prevSplicedElem;
            let nextSplicedElem;

            switch (dir) {
                case 'up':
                    if (this.activeElem !== 0) {
                        prevSplicedElem = this.elementsList.splice(this.activeElem - 1, 1)[0];
                        this.elementsList.splice(this.activeElem, 0, prevSplicedElem);
                        this.setActiveElem('prev');
                        // this.activeElem -= 1;
                    }
                    break;
                case 'down':
                    if (this.activeElem !== len - 1) {
                        nextSplicedElem = this.elementsList.splice(this.activeElem + 1, 1)[0];
                        this.elementsList.splice(this.activeElem, 0, nextSplicedElem);
                        // this.activeElem += 1;
                        this.setActiveElem('next');
                    }
                    break;
            }
            this.refreshConstructor();
        },
        changeLvlElem(dir) {
            let currentLvl = this.elementsList[this.activeElem].lvl;
            if (dir === 'up' && currentLvl < 6) {
                this.elementsList[this.activeElem].lvl = currentLvl + 1;
            } else if (dir === 'down' && currentLvl > 2) {
                this.elementsList[this.activeElem].lvl = currentLvl - 1;
            }
            this.refreshConstructor();
        },
        deleteElem() {
            this.elementsList.splice(this.activeElem, 1);
            if (this.activeElem !== false) {
                if (this.elementsList.length === 0) {
                    this.setActiveElem(false);
                } else if (this.activeElem >= this.elementsList.length) {
                    this.setActiveElem('prev');
                }
            }
            this.refreshConstructor();
        },
        cloneElem() {
            // let clonedElem = this.elementsList.slice(this.activeElem, this.activeElem + 1)[0];
            let clonedElem = {
                el: this.elementsList[this.activeElem].el.cloneNode(true),
                lvl: this.elementsList[this.activeElem].lvl
            };
            // clonedElem = clonedElem.el.cloneNode(true);
            this.elementsList.splice(this.activeElem, 0, clonedElem);
            this.refreshConstructor();
        },
        validateLayout() {
            if (this.elementsList.length > 0 && this.elementsList[0].lvl !== 2) {
                this.layoutIsValid = false;
                return;
            }

            for (let i = 1; i < this.elementsList.length; i++) {
                if ((this.elementsList[i].lvl - this.elementsList[i - 1].lvl) >= 2) {
                    this.layoutIsValid = false;
                    return;
                }
            }
            this.layoutIsValid = true;
            // this.getFinalLayout();
            return;
        },
        getFinalLayout() {
            if (!this.layoutIsValid) return;
            // let dt = document.createElement
            // console.log(document.doctype);
            // for (let i = 1; i < this.elementsList.length; i++) {

            // }
        },
        clearConstructor() {
            this.elementsList.splice(0);
            this.setActiveElem(false);
        },
        refreshConstructor() {this.$refs.constructor.refreshElems();}
    }
}
</script>

<style lang="scss">
#app {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    min-height: 100vh;
    width: 100%;
}
$main-font: 'Noto Sans JP', sans-serif;

body {
    min-height: 100vh;
    justify-content: space-between;
    flex-direction: column;
}
div, p, h1, h2, h3, h4, h5, h6, a, input, label, header, aside, menu, body {
    font-family: $main-font;
}
main {
    flex-grow: 1;
}
// input[type="email"]:focus::-webkit-input-placeholder,
// input[type="email"]:valid::-webkit-input-placeholder,
input[type="text"],
input[type="text"]::-webkit-input-placeholder,
input[type="text"]:focus::-webkit-input-placeholder {
    text-align: center;
}
input[type="text"]:disabled {
    opacity: .5;
}
button:disabled {
    opacity: .5;
}
.hljs {
    background: none;
    font-family: monospace;
    padding: 0;
}

.container {
    max-width: 1200px;
    width: 100%;
    padding-left: 15px;
    padding-right: 15px;
    margin: 0 auto;
}
.content {
    width: 100%;
}
input[type=text].invalid {
    animation: invalidValue 2s forwards;
}
@keyframes invalidValue {
    0% {
        background-color: rgb(255, 234, 234);
    }
    35% {
        background-color: rgb(224, 65, 65);
    }
    100% {
        background-color: rgb(255, 234, 234);
    }
}

.fields {
    display: flex;
    justify-content: space-between;
    width: 100%;
    &__col {
        flex-basis: 45%;
    }
    &__title {
        margin-bottom: 10px;
        text-align: center;
        font-weight: 700;
        text-transform: uppercase;
    }
    // &__item {}
}
.lvl-1 {
    margin-left: 0;
    &::before {
        width: 0;
    }
}
.lvl-2 {
    margin-left: 20px;
    &:not(.elem--disabled):not(.dragging)::before {width: 16px;}
}
.lvl-3 {
    margin-left: 40px;
    &:not(.elem--disabled):not(.dragging)::before {width: 36px;}
}
.lvl-4 {
    margin-left: 60px;
    &:not(.elem--disabled):not(.dragging)::before {width: 56px;}
}
.lvl-5 {
    margin-left: 80px;
    &:not(.elem--disabled):not(.dragging)::before {width: 76px;}
}
.lvl-6 {
    margin-left: 100px;
    &:not(.elem--disabled):not(.dragging)::before {width: 96px;}
}
</style>
